import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { withFormik } from "formik";
import * as Yup from "yup";
import { useHistory } from "react-router-dom";

import "./KnowledgeEdit.css";

import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import PageHeader from "./components/PageHeader/PageHeader";
import knowledgeApi from "api/common/knowledge";
import subMajorApi from "api/common/subMajor";
import {
  FaExchangeAlt,
  FaPlus,
  FaSync,
  FaTextWidth,
  FaTrash,
  FaUserAlt,
  FaUsersCog,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import { RoutesString } from "pages/routesString";
import { toast } from "react-toastify";
import { isErrors } from "utils/utils";

const initValues = {
  // maKKT: null,
  ten: "",
  tinChiTuChon: "",
  tinChiBatBuoc: "",
  tinChiTuChonTuDo: "",
  ghiChu: "",
  chiTietNganh: 0,
};

const KnowledgeEdit = function ({ values, handleChange, resetForm, errors }) {
  const { t } = useTranslation();

  const history = useHistory();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [listSubMajor, setListSubMajor] = useState([
    { id: "", khoa: 2000, nganhDaoTao: { ten: "" } },
  ]);

  const availableStatus = [
    {
      value: true,
      name: t("admin.common.DELETED"),
    },
    {
      value: false,
      name: t("admin.common.AVAILABLE"),
    },
  ];

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    } else {
      // initValues.maKKT = "";
      initValues.ten = "";
      initValues.tinChiTuChon = "";
      initValues.tinChiBatBuoc = "";
      initValues.tinChiTuChonTuDo = "";
      initValues.ghiChu = "";
      initValues.chiTietNganh = 0;
      const res2 = await subMajorApi.getAll();
      if (res2) {
        setListSubMajor(res2.contents);
      }
    }
  }, []);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await knowledgeApi.getById(id);
      if (res) {
        // initValues.maKKT = res.maKKT;
        initValues.ten = res.ten;
        initValues.tinChiTuChon = res.tinChiTuChon;
        initValues.tinChiBatBuoc = res.tinChiBatBuoc;
        initValues.tinChiTuChonTuDo = res.tinChiTuChonTuDo;
        initValues.ghiChu = res.ghiChu;
        initValues.chiTietNganh = res.chiTietNganh.id;
      }
      const res2 = await subMajorApi.getAll();
      if (Array.isArray(res2?.contents)) {
        setListSubMajor(res2.contents);
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    }
    setIsLoading(false);
  };
  const resetNoti = () => {
    setNotification({
      color: "success",
      isOpen: false,
      messages: [],
    });
  }
  const handleEditOnClick = async function (e) {
    e.preventDefault();
    try {
      if (isUpdatePage) {
        const res = await knowledgeApi.putById(id, values);
        if (res) {
          if (res.status === 200) {
            toast.success(t("admin.UPDATE_SUCCESS"));
            updateForm(id);
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      } else {
        const res = await knowledgeApi.post(values);
        if (res) {
          if (res.status === 201) {
            toast.success(t("admin.ADD_SUCCESS"));
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  const handleRefreshOnClick = function (e) {
    e.preventDefault();
    resetNoti();
    resetForm();
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await knowledgeApi.deleteById(id);
      if (res) {
        if (res.status === 200) {
          toast.success(t("admin.common.DELETED"));
          history.push(RoutesString.ADMIN_KNOWLEDGE_LIST)
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || err?.response?.status === 401) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.knowledge_edit.UPDATE")
            : t("admin.knowledge_edit.ADD")
        }
        items={[
          {
            href: RoutesString.ADMIN_KNOWLEDGE_LIST,
            title: t("admin.knowledge_list.LIST"),
          },
        ]}
      />

      <PageHeader
        headerTitle={
          isUpdatePage
            ? t("admin.knowledge_edit.UPDATE")
            : t("admin.knowledge_edit.ADD")
        }
      />

      <div className="page-content">
        <Form>
          <Notification
            color={notification.color}
            isOpen={notification.isOpen}
            messages={notification.messages}
          />
          {/* <FormGroup className="row">
            <Label for="value" className="col-lg-3">
              {t("admin.knowledge_edit.CODE")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaUserAlt />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="maKKT"
                  placeholder={t("admin.knowledge_edit.CODE_PLACEHOLDER")}
                  value={values.maKKT}
                  onChange={handleChange}
                  invalid={errors.maKKT}
                  readOnly={isUpdatePage}
                />
              </InputGroup>
              {errors.maKKT && (
                <FormText color="danger">{errors.maKKT}</FormText>
              )}
            </div>
          </FormGroup>{" "} */}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.knowledge_edit.NAME")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="ten"
                  placeholder={t("admin.knowledge_edit.NAME_PLACEHOLDER")}
                  value={values.ten}
                  onChange={handleChange}
                  invalid={errors.ten}
                />
              </InputGroup>
              {errors.ten && <FormText color="danger">{errors.ten}</FormText>}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.knowledge_list.OBLIGATORY_CREDIT")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="number"
                  name="tinChiBatBuoc"
                  placeholder={t(
                    "admin.knowledge_edit.OBLIGATORY_CREDIT_PLACEHOLDER"
                  )}
                  value={values.tinChiBatBuoc}
                  onChange={handleChange}
                  invalid={errors.tinChiBatBuoc}
                />
              </InputGroup>
              {errors.tinChiBatBuoc && (
                <FormText color="danger">{errors.tinChiBatBuoc}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.knowledge_list.OPTIONAL_CREDIT")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="number"
                  name="tinChiTuChon"
                  placeholder={t(
                    "admin.knowledge_edit.OPTIONAL_CREDIT_PLACEHOLDER"
                  )}
                  value={values.tinChiTuChon}
                  onChange={handleChange}
                  invalid={errors.tinChiTuChon}
                />
              </InputGroup>
              {errors.tinChiTuChon && (
                <FormText color="danger">{errors.tinChiTuChon}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.knowledge_list.FREE_OPTIONAL_CREDIT")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="number"
                  name="tinChiTuChonTuDo"
                  placeholder={t(
                    "admin.knowledge_edit.FREE_OPTIONAL_CREDIT_PLACEHOLDER"
                  )}
                  value={values.tinChiTuChonTuDo}
                  onChange={handleChange}
                  invalid={errors.tinChiTuChonTuDo}
                />
              </InputGroup>
              {errors.tinChiTuChonTuDo && (
                <FormText color="danger">{errors.tinChiTuChonTuDo}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.knowledge_list.NOTE")}
              {/* <AsteriskIcon /> */}
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="text"
                  name="ghiChu"
                  placeholder={t("admin.knowledge_edit.NOTE_PLACEHOLDER")}
                  value={values.ghiChu}
                  onChange={handleChange}
                  invalid={errors.ghiChu}
                />
              </InputGroup>
              {errors.ghiChu && (
                <FormText color="danger">{errors.ghiChu}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup className="row">
            <Label for="name" className="col-lg-3">
              {t("admin.knowledge_edit.MAJOR_DETAIL")} <AsteriskIcon />
            </Label>
            <div className="col-lg-6">
              <InputGroup>
                <InputGroupAddon addonType="prepend">
                  <InputGroupText>
                    <FaTextWidth />
                  </InputGroupText>
                </InputGroupAddon>
                <Input
                  type="select"
                  name="chiTietNganh"
                  value={values.chiTietNganh}
                  onChange={handleChange}
                >
                  <option key={1} value={0}>
                    {t("admin.knowledge_edit.CHOOSE_MAJOR_DETAIL")}
                  </option>
                  {listSubMajor.length > 0 &&
                    listSubMajor.map((item, index) => {
                      return (
                        <option key={index + 1} value={item.id}>
                          {item?.nganhDaoTao?.chuongTrinhDaoTao?.maCTDT}
                          {/* {t("admin.knowledge_list.MAJORS_NAME")}:{" "} */}
                          &ensp;{"/"}
                          {item.nganhDaoTao?.ten}&ensp;{"/"}&ensp;
                          {t("admin.knowledge_list.COURSE")}: 
                          {item.khoa}&ensp;
                        </option>
                      );
                    })}
                </Input>
              </InputGroup>
              {errors.chiTietNganh && (
                <FormText color="danger">{errors.chiTietNganh}</FormText>
              )}
            </div>
          </FormGroup>{" "}
          <br />
          <FormGroup>
            <Button color="success" onClick={handleEditOnClick} disabled={isErrors(errors)}>
              {isUpdatePage ? (
                <>
                  <FaExchangeAlt /> {t("admin.common.UPDATE")}
                </>
              ) : (
                <>
                  <FaPlus /> {t("admin.common.ADD")}
                </>
              )}
            </Button>
            &emsp;
            {isUpdatePage && (
              <>
                <Button onClick={handleRefreshOnClick}>
                  <FaSync /> {t("admin.common.REFRESH")}
                </Button>
                &emsp;
                <Button color="danger" onClick={handleDeleteOnClick}>
                  <FaTrash /> {t("admin.common.DELETE")}
                </Button>
              </>
            )}
          </FormGroup>
        </Form>
      </div>
    </div>
  );
};

const KnowledgeEditFormikForm = withFormik({
  mapPropsToValues() {
    return initValues;
  },
  enableReinitialize: true,
  validationSchema: Yup.object().shape({
    // maKKT: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    // ghiChu: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
    tinChiTuChon: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .min(0, i18n.t("admin.POSITIVE_WARNING")),
    tinChiBatBuoc: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .min(0, i18n.t("admin.POSITIVE_WARNING")),
    tinChiTuChonTuDo: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .min(0, i18n.t("admin.POSITIVE_WARNING")),
    chiTietNganh: Yup.number()
      .required(i18n.t("REQUIRED_FIELD_WARNING"))
      .positive(i18n.t("REQUIRED_FIELD_WARNING")),
  }),
})(KnowledgeEdit);

export default KnowledgeEditFormikForm;
